* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: sans-serif;
}

body {
  padding: 0;
  margin: 0;
  min-height: 100%;
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(225,225,225,1)), to(rgba(105,105,105,1)));
  background: -webkit-linear-gradient(bottom, rgba(225,225,225,1) 0%, rgba(105,105,105,1) 100%);
  background: -o-linear-gradient(bottom, rgba(225,225,225,1) 0%, rgba(105,105,105,1) 100%);
  background: linear-gradient(0deg, rgba(225,225,225,1) 0%, rgba(105,105,105,1) 100%);
}

html, #root {
  padding: 0;
  margin: 0;
  min-height: 100%;
}


